import React, { Component } from "react";
import axios from "axios";
import SearchArea from "../mySearchArea/mySearchArea";
import MovieList from "../Movie List/movie_list_Results";
import "./home.css";
import {  Row, Container } from "react-bootstrap";
import { Redirect} from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const baseImgUrl = "https://image.tmdb.org/t/p/w500";
let myKey = "d759a614b16c0c1c0295b4313e94aeec";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      moviesTop: [], // home-movies
      moviesTop2: [], 
      moviesTop3: [], 
      movies: [], // home-search-movies
      isLoaded: false,
      searchTerm: "", //to search Helper
      tvShowsPopular: [],//tv show array
      tvShowsPopular2: [],
      tvShowsPopular3: [],
      totalResuolts: 0,// search 
      currentPage: 1,
      movie: null,
      flag: false,//showpage
      flag2: false,//about
      user: JSON.parse(sessionStorage.getItem("user") || null)
    };
    console.log("constructor is working");
  }
  // __________movie and TVshow____________
  async componentDidMount() {
    const response = await axios.get("http://localhost:3000/home")
    const moviesData = response.data.movies;
    console.log("componentDidMount page 1 start working");
    this.setState({ moviesTop: moviesData });
// _____
    const response2 = await axios.get("http://localhost:3000/home/two")
    const moviesData2 = response2.data.movies;
    this.setState({ moviesTop2: moviesData2 });
// _____
    const response3 = await axios.get("http://localhost:3000/home/three")
    const moviesData3 = response3.data.movies;
    this.setState({ moviesTop3: moviesData3 });
// _____
    const res = await axios.get("http://localhost:3000/home/tv")
    const tvshowData = res.data.tvshow;
    this.setState({ tvShowsPopular: tvshowData });
// _____
    const res2 = await axios.get("http://localhost:3000/home/tv/two")
    const tvshowData2 = res2.data.tvshow;
    this.setState({ tvShowsPopular2: tvshowData2 });
// _____
    const res3 = await  axios.get("http://localhost:3000/home/tv/three")
    const tvshowData3 = res3.data.tvshow;
    console.log("componentDidMount page 6 is finish update the Movies in Home.js");
    this.setState({ tvShowsPopular3: tvshowData3 });
  }

  // _______________________

  handleSubmit = e => // form
  {
    e.preventDefault();
    axios
      .get(
        `https://api.themoviedb.org/3/search/multi?api_key=${myKey}&query=${this.state.searchTerm}`
      )

      .then(res => {
        console.log(res.data.results);
        this.setState({
          movies: [...res.data.results],
          totalResuolts: res.data.total_results
        });
      })

      .catch(err => {
        console.log(err);
      });
    console.log("search is done");
  };
  
  //searchTerm change value 
  handleChange = e => { // input
    this.setState({ searchTerm: e.target.value });
  };
  // _______________________
  movieInfo = movie => {
    this.setState({
      flag: true,
      movie: movie
    }); 
  };
  // _______________________

  addItemToList = movieObj => {
    if (sessionStorage.length > 0) {
      axios
        .post(`/cart/${this.state.user._id}`, {
          movie: movieObj

        })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    } else {
      this.setState({flag2 : true})
      
    }
  };

  render() {
    
    if (this.state.flag) {
      return (
        <Redirect
          to={{
            pathname: "/ShowPage",
            movie: this.state.movie
          }}
        />
      );
    }

    if (this.state.flag2) {
      return (
        <Redirect
          to={{
            pathname: "/About",
            // redirect to user who not register.
          }}
        />
      );
    }



// carussel abilty
    const settings = {
      infinite: true,
      speed: 650,
      slidesToShow: 5,
      slidesToScroll: 4,
      arrows: true
    };

    const carusselMovies = this.state.moviesTop.map((movie, index) => (
      <div className="carussel-card" key={index}>
        <img src={`${baseImgUrl}/${movie.poster_path}`} onClick={() => this.movieInfo(movie)} alt=""/>
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
    ));

    const carusselMovies2 = this.state.moviesTop2.map((movie, index) => (
      <div className="carussel-card"  key={index}>
        <img src={`${baseImgUrl}/${movie.poster_path}`} onClick={() => this.movieInfo(movie)} alt=""/>
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
    ));

    const carusselMovies3 = this.state.moviesTop3.map((movie, index) => (
      <div className="carussel-card"  key={index}>
        <img src={`${baseImgUrl}/${movie.poster_path}`} onClick={() => this.movieInfo(movie)}alt="" />
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
    ));


    const carusselTvShow = this.state.tvShowsPopular.map((movie, index) => (
      <div className="carussel-card"  key={index}>
        <img src={`${baseImgUrl}/${movie.poster_path}`} onClick={() => this.movieInfo(movie)} alt=""/>
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
    ));

    const carusselTvShow2 = this.state.tvShowsPopular2.map((movie, index) => (
      <div className="carussel-card"  key={index}>
        <img src={`${baseImgUrl}/${movie.poster_path}`} onClick={() => this.movieInfo(movie)} alt="" />
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
    ));

    const carusselTvShow3 = this.state.tvShowsPopular3.map((movie, index) => (
      <div className="carussel-card"  key={index}>
        <img src={`${baseImgUrl}/${movie.poster_path}`} onClick={() => this.movieInfo(movie)} alt=""/>
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
    ));


    
    //------------------------------------------------------------
    return (
      <div className="Home ">
        <h1 id="header-title">Welcome To Fox Movie</h1>

        <Container>
          <SearchArea
            handleSubmit={this.handleSubmit}
            handleChange={this.handleChange}
          />
        </Container>

        <Container>
        {!this.state.totalResuolts ? <div className="col-md-12 gener-title"> Movies upcoming </div> : ""}
        {!this.state.totalResuolts ? <Slider {...settings}>{carusselMovies}</Slider> : ""}  
        </Container>

        <Container>
        {!this.state.totalResuolts ? <div className="col-md-12 gener-title"> Movies Popular </div> : ""}
        {!this.state.totalResuolts ? <Slider {...settings}>{carusselMovies2}</Slider> : ""} 
        </Container>

        <Container>
        {!this.state.totalResuolts ? <div className="col-md-12 gener-title"> Movies Top Rated </div> : ""}
        {!this.state.totalResuolts ? <Slider {...settings}>{carusselMovies3}</Slider> : ""} 
        </Container>


        <Container>   
        {!this.state.totalResuolts ? <div className="col-md-12 gener-title"> Popular TV Series  </div> : ""}
        {!this.state.totalResuolts ? <Slider {...settings}>{carusselTvShow}</Slider> : ""} 
        </Container>

        <Container>
        {!this.state.totalResuolts ? <div className="col-md-12 gener-title">Updated TV Series</div> : ""}
        {!this.state.totalResuolts ? <Slider {...settings}>{carusselTvShow2}</Slider> : ""} 
        </Container>

        <Container>
        {!this.state.totalResuolts ? <div className="col-md-12 gener-title">Top Rated TV Series </div> : ""}
        {!this.state.totalResuolts ? <Slider {...settings}>{carusselTvShow3}</Slider> : ""} 
        </Container>

        <Container>
          <Row>
            <MovieList movies={this.state.movies} />
          </Row>
        </Container>
      </div>
    );
  
  }
}

export default Home;
