import React, { Component } from 'react'
import "./showPage.css";
import { Redirect } from 'react-router-dom';


class ShowPage extends Component {
    state = { flag : false, homePage : null }

    movie = this.props.location.movie;

    BackToHome = homePage =>{
      this.setState({flag : true, homePage : homePage});
    };

    render() {
      if(this.state.flag){
        return(

          <Redirect to ={{pathname :"/",
        }}
        />
        )
      };
        console.log(`this movie click : ${this.movie.title}`);
        return (
            
            <div className="movieInfo">
            <div className="movieContent">
              <img className="poster" src={`https://image.tmdb.org/t/p/w300${this.movie.poster_path}`} alt=""/>
              <h4 className="movieHeader">{this.movie.title ? this.movie.title : this.movie.name}</h4>
              <p className="headText">Over View </p>
              <p className="movieText">{this.movie.overview}</p>
              <span className="score-buble1">{this.movie.vote_average}</span>
                  
            </div>
              <button id="goBack" onClick={()=> this.BackToHome()}>Back To Home</button>
          </div>
            
        
        )
    } 


}
export default ShowPage;